class app{
    constructor(){
        this.$root=$('#app');
        this.dayChooseList=[];
        this.start();
        this.load();
    }
    //获取音乐分类
    start(){
        let _this = this;
        $.getJSON('https://jirenguapi.applinzi.com/fm/getChannels.php')
        .done(xxx=>{
            _this.renderHotList(xxx)
        }).fail(()=>{
          console.log('error')
        })
    }
    //渲染热歌
    renderHotList(ccc){
        let xxx=ccc.channels
        let tpl = '' 
        for(let i = 0 ; i < xxx.length;i++){
            tpl +=  '<li class="item" data-channel-id="' + xxx[i].channel_id + '" data-channel-name = "' + xxx[i].name +'">'
            tpl += '  <div class="img" style="background-image:url(' + xxx[i].cover_small + ')"></div>'
            tpl +=  '<h4>'+ xxx[i].name + '</h4>'
            tpl +=  '</li>'
        }
        this.$root.find('.hotList .box').append($(tpl))
    }
    //加载热歌
    load(){
        let _this=this
        for(let i = 0 ; i <4;i++){
            $.getJSON('https://jirenguapi.applinzi.com/fm/getSong.php',{channel: 'public_tuijian_spring'})
            .done(xxx=>{
                let x=xxx.song[0]
                _this.dayChooseList.push(x);
                let node =  `<li>
                                <img src= "${x.picture}">
                                <svg class="play" xmlns:xlink="http://www.w3.org/1999/xlink" aria-hidden="true">
                                    <use xlink:href="#icon-play"/>
                                </svg>
                                <h5>${x.title}</h5>
                                <svg class="sq" aria-hidden="true">
                                    <use xlink:href="#icon-sq"></use>
                                </svg>
                                <p>${x.artist}</p>
                            </li>`
                _this.$root.find('.dayChoose ul').append($(node))
            })
        }
        //console.log(_this.dayChooseList=)

    }
}
new app()